.container {
  width: 600px;
  display: flex;
  flex-direction: column;
  background: white;
  margin: auto;
  margin-top: 200px;
  padding-bottom: 30px;

}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  width: 100%;
  gap: 10px;
}

.text {
  font-size: 40px;
  font-weight: 700;
  color: #2d4dfd;
}

.underline {
  width: 60px;
  height: 5px;
  background: #2d4dfd;
  border-radius: 10px;
}

.inputs {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  gap: 20px;

}

.input {
  display: flex;
  align-items: center;
  width: 480px;
  height: 80px;
  background: #eaeaea;
  margin: auto;
  border-radius: 6px;

}

.input img {
  margin: 0 30px;
}

.input input {
  width: 400px;
  height: 50px;
  background: transparent;
  outline: none;
  border: none;
  color: #797979;
  font-size: 20px;
}

.forget-password {
  margin-top: 25px;
  padding-left: 75px;
  color: #797979;
  font-size: 18px;
}

.forget-password span {
  color: #2d4dfd;
  cursor: pointer;

}

.submit-container {
  display: flex;
  gap: 30px;
  margin: 50px auto;

}

.submit {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 45px;
  border-radius: 50px;
  color: white;
  font-size: 18px;
  font-weight: 700;
  background-color: #2d4dfd;
  cursor: pointer;

}

.gray {
  background: #eaeaea;
  color: #676767;
}